<template>
  <div class="hello">
    <br> <br>
    <div class="card" id="inner">
      <img
        style="width:100%" v-if="this.search === 'confirmed'"
        src="https://cdnuploads.aa.com.tr/uploads/Contents/2020/06/09/thumbs_b_c_4e3e823174d74ac9a73c218df8e700dd.jpg?v=194715"
      />
      <img
        style="width:100%" v-if="this.search === 'deaths'"
        src="https://scx1.b-cdn.net/csz/news/800/2016/howscaredofd.jpg"
      />
      <img style="width:100%" 
        v-if="this.search === 'recovered'"
        src="https://i.insider.com/5e7ba2f5487c2211ce375394?width=1100&format=jpeg&auto=webp"
      />

      <div class="container">
        <h4>
          <b>{{this.search}} in {{this.currentTime}}</b>
        </h4>
        <table>
          <tr>
            <th>Index</th>
            <th>Country</th>
            <th>{{this.search}}</th>
          </tr>

          <tr v-for="countryInfo in countryInfos" :key="countryInfo.country">
            <td>{{countryInfo.rank}}</td>
            <td>{{countryInfo.country}}</td>
            <td>{{countryInfo.info}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    countries: Number,
    search: String
  },
  data() {
    return {
      covid: {},
      countryInfos: [],
      currentTime: ""
    };
  },
  async created() {
    const response = await fetch("https://api.covid19api.com/summary");
    const myData = await response.json();
    let countryInfos = [];
    if (this.search == "confirmed") {
      for (let i = 0; i < myData.Countries.length; i++) {
        countryInfos.push({
          country: myData.Countries[i].Country,
          info: myData.Countries[i].TotalConfirmed
        });
      }
    } else if (this.search == "deaths") {
      for (let i = 0; i < myData.Countries.length; i++) {
        countryInfos.push({
          country: myData.Countries[i].Country,
          info: myData.Countries[i].TotalDeaths
        });
      }
    } else if (this.search == "recovered") {
      for (let i = 0; i < myData.Countries.length; i++) {
        countryInfos.push({
          country: myData.Countries[i].Country,
          info: myData.Countries[i].TotalRecovered
        });
      }
    }
    countryInfos.sort(function(a, b) {
      return b.info - a.info;
    });
    let finalResult = [];
    for (let i = 0; i < this.countries; i++) {
      finalResult.push({
        country: countryInfos[i].country,
        info: countryInfos[i].info
      });
    }
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1;
    var yyyy = today.getFullYear();
    if (dd < 10) dd = "0" + dd;
    if (mm < 10) mm = "0" + mm;
    this.currentTime = `${dd}/${mm}/${yyyy}`;
    this.countryInfos = finalResult;

    console.log("Checked covid into: ", countryInfos);
  },
  async updated() {
    const response = await fetch("https://api.covid19api.com/summary");
    const myData = await response.json();
    let countryInfos = [];
    if (this.search == "confirmed") {
      for (let i = 0; i < myData.Countries.length; i++) {
        countryInfos.push({
          country: myData.Countries[i].Country,
          info: myData.Countries[i].TotalConfirmed
        });
      }
    } else if (this.search == "deaths") {
      for (let i = 0; i < myData.Countries.length; i++) {
        countryInfos.push({
          country: myData.Countries[i].Country,
          info: myData.Countries[i].TotalDeaths
        });
      }
    } else if (this.search == "recovered") {
      for (let i = 0; i < myData.Countries.length; i++) {
        countryInfos.push({
          country: myData.Countries[i].Country,
          info: myData.Countries[i].TotalRecovered
        });
      }
    }
    countryInfos.sort(function(a, b) {
      return b.info - a.info;
    });
    let finalResult = [];
    for (let i = 0; i < this.countries; i++) {
      finalResult.push({
        rank: i + 1,
        country: countryInfos[i].country,
        info: countryInfos[i].info
      });
    }
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth() + 1;
    var yyyy = today.getFullYear();
    if (dd < 10) dd = "0" + dd;
    if (mm < 10) mm = "0" + mm;
    this.currentTime = `${dd}/${mm}/${yyyy}`;
    this.countryInfos = finalResult;

    console.log("Checked covid into: ", countryInfos);
  }
};
</script>

